<template>
    <div class="tac">
        <el-menu default-active="displayPage"
            class="el-menu-vertical-demo"
            router="router"
            unique-opened>
            <el-submenu v-for="item in menu" :index="item.id" :key="item.id">
                <template slot="title">
                    <span v-text="item.name"></span>
                </template>
                <el-menu-item-group class="over-hide" v-for="sub in item.sub" :key="sub.componentName">
                    <el-menu-item :index="sub.componentName" v-text="sub.name" :route="{name:sub.componentName}" />
                </el-menu-item-group>
            </el-submenu>

        </el-menu>
    </div>
</template>

<script>
import menu from '@/config/menu-config'

export default {
    data () {
        return {
            menu: menu
        }
    },
    computed: {
        displayPage () {
            return this.$route.path
        }
    },
    methods: {
        // @open="handleOpen"
        //     @close="handleClose"
        // handleOpen (key, keyPath) {
        //     console.log(key, keyPath)
        // },
        // handleClose (key, keyPath) {
        //     console.log(key, keyPath)
        // }
    }
}
</script>
<style scoped>
.el-menu-vertical-demo {
    display: inline-block;
    text-align: left;
}
.tac {
    width: 240px;
}
.tac .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 240px;
    min-height: 400px;
}
</style>
<style>
.el-submenu .el-menu-item {
    height: 40px;
    line-height: 40px;
}
.el-submenu.is-active .el-submenu__title {
    border-bottom-color: #00C4C0;
}
.el-menu-item, .el-submenu__title {
    height: 54px;
    line-height: 54px;
    color: #333;
}
.el-submenu.is-opened .el-submenu__title,
.el-submenu.is-opened .el-submenu__title i {
    color: #00C4C0;
}
.el-submenu__title i {
    color: #333;
    font-size: 15px;
}
.el-submenu__title:hover {
    background-color: #fff;
    color: #00C4C0;
}
.el-menu .el-menu-item-group {
    background-color: #f6f9fc;
}
.el-menu-item.is-active {
    color: #fff;
    background-color: #00C4C0;
}
.el-submenu__title:hover i {
    color: #00C4C0;
}
.el-menu-item:hover {
    color: #00C4C0;
    background-color: #f6f9fc;
}
.el-menu-item:focus {
    outline: 0;
    color: #fff;
    background-color: #00C4C0;
}
</style>



